<template>
    <div>
        <form @submit.prevent="addTodo">
            <input type="text" name="title" placeholder="Add Todo..." v-model="title">
            <input type="submit" value="Submit" class="btn" id="submit">
            <label for="submit" class="add"><span class="add__btn"></span></label>
        </form>
    </div>
</template>

<script>
export default {
    name: 'AddTodo',
    data() {
        return {
            title: ''
        }
    },
    methods: {
        addTodo() {
            if ( !this.title.trim().length ) {
                return
            }

            const newTodo = {
                id: Date.now(),
                title: this.title,
                completed: false
            };

            this.$emit('add-todo', newTodo);

            this.title = '';
        }
    }
}
</script>

