<template>
  <div id="app-wrapper">
    <div class="wrapper">
      <div class="todolist">
        <Header />
        <TasksQuantity :todos="todos"/>
        <Cta v-if="!todos.length"/>
        <Todos :todos="todos" @del-todo="deleteTodo"/>
        <AddTodo @add-todo="addTodo" />
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../components/layout/Header';
import TasksQuantity from '../components/TasksQuantity';
import Cta from '../components/Cta';
import Todos from '../components/Todos';
import AddTodo from '../components/AddTodo';

export default {
  name: 'Home',
  components: {
    Header,
    TasksQuantity,
    Cta,
    Todos,
    AddTodo
  },
  data() {
    return {
      todos: []
      // todos: JSON.parse(window.localStorage.getItem('TODO'))
    }
  },
  methods: {
    deleteTodo(id) {
      this.todos = this.todos.filter( todo => todo.id !== id );
      let todoArr = JSON.parse( window.localStorage.getItem('TODO') );
      
      todoArr = todoArr.filter( todo => todo.id !== id);
      window.localStorage.setItem('TODO', JSON.stringify(todoArr) );
    },
    addTodo(newTodo) {
      this.todos = [...this.todos, newTodo];
      window.localStorage.setItem( 'TODO', JSON.stringify(this.todos) );

      console.log( this.todos );
    }
  },
  created() {
    let parsed = JSON.parse(window.localStorage.getItem('TODO'));

    this.todos = parsed ? JSON.parse(window.localStorage.getItem('TODO')) : []; // try to get rid of [] and just leave JSON...
  }
}
</script>

