<template>
    <div>
        <div class="quantity">
            <div class="quantity__created">
                <p class="quantity__value">{{ getCreated(todos) }}</p>
                <p class="quantity__descr">Created tasks</p>
            </div>
            <div class="quantity__completed">
                <p class="quantity__value">{{ getCompleted(todos) }}</p>
                <p class="quantity__descr">Completed tasks</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'TasksQuantity',
    props: ['todos'],
    methods: {
        getCreated(todos) {
            let length = (todos.length === 0) ? todos.length : (todos.length < 10) ? '0' + todos.length : todos.length;
            return length;
        },
        getCompleted(todos) {
            let completed = todos.filter( todo => todo.completed === true);
            let length = (completed.length === 0) ? completed.length : (completed.length < 10) ? '0' + completed.length : completed.length;
            return length;
        }
    }
}
</script>

