<template>
    <div class="todo-item" :class="{'is-completed': todo.completed}">
        <input type="checkbox" @change="markComplete" :checked="todo.completed" :id="todo.id">
        <label :for="todo.id">
            <span class="check"></span>
            <span class="title">{{ todo.title }}</span>
            </label>
        <div class="del" @click="$emit('del-todo', todo.id)">
            <span></span>
        </div>
    </div>
</template>

<script>

export default {
    name: 'TodoItem',
    props: ['todo'],
    methods: {
        markComplete() {
            this.todo.completed = !this.todo.completed;
            let todoArr = JSON.parse( window.localStorage.getItem('TODO') );
            let index = todoArr.findIndex( el => el.id === this.todo.id );

            todoArr[index].completed = this.todo.completed;
            window.localStorage.setItem('TODO', JSON.stringify(todoArr) );
        }
    }
}
</script>

