<template>
    <header class="header">
        <div class="date">
            <h1 class="date__weekday date__weekday_bold">{{ getWeekDay() + ', '}}</h1>
            <span class="date__actual-date">{{ getActualDate() }}</span>
        </div>
        <!-- <h1>{{ getWeekDay() + ', ' + getActualDate() }}</h1> -->
    </header>
</template>

<script>
export default {
    name: 'Header',
    methods: {
        getWeekDay() {
            let date = new Date();
            let options = {
                weekday: 'long'
            };

            date = date.toLocaleString('en-US', options);
            return date;
        },
        getActualDate() {
            let date = new Date();
            let options = {
                day: 'numeric',
                month: 'short'
            };

            date = date.toLocaleString('en-US', options);
            return date;
        }
    }
}
</script>

